            <!-- <i :class="['iconfont',!!lookAuthor == true?'commentActive':'']" @click="changeLookAuthor">&#xe618;</i> -->
            <div @click="changeCommentSort('first')" :class="['commentSort',commentSort == 'first'?'commentActive':'']">最先评论</div>
            <div @click="changeCommentSort('last')" :class="['commentSort',commentSort == 'last'?'commentActive':'']">最后评论</div>
