            <!-- <HeadPor  class="headPor"
            :msgg="{ authen:item.user_authen, id:item.comment_user_id,img:item.user_img,name:item.user_name,user_lv:item.user_lv,gold:item.user_gold}"></HeadPor> -->

            <!-- 下部 -->
            <div class="bottom">
              <div>
                <!-- 时间  -->
