                <span >
                    {{item.comment_index}}楼
                </span>
              </div>
            </div>

            <!-- //////////////是否回复了评论-->
            <div class="replyComment" v-if="item.comment_reply_comment_id">
              <div>回复 : {{item.replyTemp.user_name}}</div>
              <!-- 被回复的评论内容 class="re_content"-->
              <div
              :class="['re_content',!!item.replyTemp.comment_content==false?'del':'']">
                {{item.replyTemp.comment_content|fNoComment}}
              </div>
            </div>
            <!-- /////评论内容///////// ///////////////-->
            <div :class="['commentContent',!!item.comment_content==false?'del':'']">
              {{item.comment_content|fNoComment}}
            </div>

            <!-- </div> -->
            <!-- /////////////////bottom ///////////////////////////////////////////////-->


        </div>
        </van-list>
        <!-- </van-pull-refresh> -->

    </div>
    <!-- 底部评论事件 ////////////////////////////////////////////////////////-->

    <!-- 1111111111111111111111111111111111111-->
    <!-- 底部评论事件 ////////////////////////////////////////////////////////-->
    <div class="positionCol" >
        <!-- 已收藏 -->
        <!-- <van-icon name="like" size="30" color="red" class="inCol"
        v-show="hasCol && !ws_colHandler" @click="colHandler"/> -->
        <i class="iconfont has_col"  v-show="hasCol && !ws_colHandler" @click="colHandler">&#xe61e;</i>
        <!-- 未收藏 -->
        <!-- <van-icon name="like-o" size="30" color="#666" class="inCol"
        v-show="!hasCol && !ws_colHandler" @click="colHandler"/> -->
        <i class="iconfont no_col col" v-show="!hasCol && !ws_colHandler" @click="colHandler">&#xe61e;</i>
        <!-- 加载中 -->
        <!-- <van-loading type="spinner" color="#1989fa" v-show="ws_colHandler && hasLogin"/> -->
        <i class="iconfont no_col col" v-show="!hasLogin" @click="colHandler">&#xe61e;</i>
    </div>

    <div class="positionComment" @click="goComment">
      <i class="iconfont inComment">&#xe61d;</i>
      <span></span>
    </div>
