<script>
  /* 引入文章处理组件*/
  import ArticleHead from "@/components/utils/ArticleHead"
  /* 引入头像组件*/
  // import HeadPor from "@/components/utils/HeadPor"
  import UserTag from "@/components/utils/UserTag"
	/* 引入导航栏组件*/
	// import SignUp from "@/components/me/utils/SignUp"

  import {createNamespacedHelpers} from 'vuex'
  // const popup=createNamespacedHelpers('popup')
  const navBar_Every=createNamespacedHelpers('navBar_Every')
  const login=createNamespacedHelpers('login')
  const readAndEditor=createNamespacedHelpers('readAndEditor')
  const list=createNamespacedHelpers('list')
  export default{
    name:"read",
    beforeRouteLeave:function(to, from, next){
      // 取消popup展示
      this.showPopupForCommentModel = false//点击评论区域弹出层
      this.showPopupForArticleModel = false //点击文章弹出层

      this.showPopupForChatModel = false //狭义评论弹出层
      this.showPopupAuthenStopUserModel = false //有权限的作者禁言用户
      this.showPopupAdminStopUserModel = false //管理员封禁用户
      this.showPopupForRewardModel  = false //打赏层

      //缓冲消失
      // this.isBuffering = false
      next()
    },
    activated(){
      if(this.firstEnter >1 && (this.fromPath != '/otherUser'
      && this.fromPath != '/getArticleReward'
      && this.fromPath != '/search')){

        this.isBuffering = true
        /* 如果不是在第一次进入并且...*/
        this.mountedFn()

        /* 下面触发评论liist加载*/
        // 清空list
        this.list.length = 0
        this.onLoad()
      }else{
        this.firstEnter ++
      }
    },
    /* 数据*/
    data(){
      return {
        popUpHeight:'50%',
        // 文章
        article : {},
        // 文章作者id
        articleUserId:undefined,
        // 文章评论
        list : [],
        // 用户是否已经收藏了该文章
        hasCol : false,
        //收藏
        // Waiting_res : true,
        ws_colHandler : true,
        // 我是该文章作者
        IAmAuthor : false,
        // 弹出框
        // dialogShow : false,
        //等待后台获取用户信息
        // Waiting_res_loginUserGold : true,
        //打赏中
        // Waiting_res_reward : false,
        // 登录者的金币数量
        // loginUserGold : 0,
        // 打赏界面的整数
        // digit:undefined,
        //错误提示
        // errNum : undefined,
        loading: false,
        finished: false,
        // isLoading: false,
        //得到文章返回结果
        getArtRes :  false,
        // 文章的图片链接
        imgsSrc : [],
        /* 展示更多 匹配*/
        showMoreOption:'',
        /* 等待删除响应*/
        // waiting_res_delComment : false,
        ws_delComment : false,
        /* 点击评论弹出层  开始*************************开始*********/
        showPopupForCommentModel : false,
        //temp_comment_id
        reply_comment_id:'',//被点击评论的评论id
        //temp_user_name:'',
        reply_user_name : '',//被点击评论用户姓名
        //temp_comment_user_id:'',
        reply_comment_user_id:'',//被点击评论用户id或者被针对的用户id
        /* 评论弹出层  结束***************************结束*******/
        /* 文章弹出层 开始****************************开始******/
        showPopupForArticleModel : false,
        ws_delArticle : false,
        //temp_article_content : '',//文章在mysql中的路径
         /* 文章弹出层 结束***************************结束*******/
         /* 是否只看作者*/
         lookAuthor : false,
         /* 狭义评论弹出层 开始**********************开始******/
         showPopupForChatModel : false,
         message : '',//留言
         //waiting_res : '',
         ws_sendComment:false,
         commentNum : 0,//留言字数
         /* 评论弹出层 *******************************结束***/
         /* 作者禁止用户评论 ********************************开始********/
         showPopupAuthenStopUserModel : false,
         showPopupAdminStopUserModel : false,//管理员封禁用户
         AdminStopUserFrom : '', //点击封禁的来源
         ws_authorStopUser:false,
         ws_adminStopUser :false,
         /* 作者禁止用户评论 ********************************结束*/
         /* 打赏开始*/
         showPopupForRewardModel : false,
         inputVal : '',
         ws_reward : false,
         /* 打赏结束*/
         /* 第一次进入页面*/
         firstEnter : 0,
         /* 举报 开始*/
         showPopupForReportModel : false,
         // radio_report : 'not18',
         ws_radio_report: false,
         /* 举报 结束*/
         /* 点击文章还是评论*/
         clickAOrC : ''
      }
    },
    filters:{
      // fb : function(value){
      //   if(value){
      //     return  value
      //   }else{
      //     /* 如果value是假 */
      //     return '评论飞到其他星球了'
      //   }
      // }
    },
    /* 方法*/
    methods:{
      //popupForArticle是点击文章主题弹出的popup
      //showPopupForComment是点击评论主题弹出的popup
      /*
        点击评论弹出框执行的函数
      */
      commentShowWhatPopup(value){
        this.showPopupForCommentModel = false
        switch(value){
          case 'report'://举报
            this.showPopupForReportModel = true
          break;
          case 'authorStopUser'://作者禁言用户
            this.showPopupAuthenStopUserModel = true
          break;
          case 'reply'://评论
            this.showPopupForChatModel = true
          break;

          case 'fn_delComment'://删除评论
            this.fn_delComment()
          break;

          case 'adminStopUser'://点击评论封号
            // this.AdminStopUserFrom = 'comment'
            this.showPopupAdminStopUserModel = true
          break;
        }
      },
      /* 文章展现什么popup
      fn_reward fn_toEditor  article
      */
      articleShowWhatPopup(value){
        this.showPopupForArticleModel = false
        switch(value){
          case 'report'://举报
            this.showPopupForReportModel = true
          break;
          case 'fn_reward'://打赏
            this.showPopupForRewardModel = true
          break;

          case 'fn_bidding'://竞标
            this.fn_bidding()
          break;

          case 'fn_toEditor'://编辑
            this.fn_toEditor()
          break;

          case 'fn_delArticle'://删除文章
            this.fn_delArticle()
          break;

          /* 设置文章tag*/
          case 'article_is_good_0'://取消文章精品
            this.setArticleTag(['article_is_good',0])
          break;
          case 'article_is_good_1'://设置文章精品
            this.setArticleTag(['article_is_good',1])
          break;

          case 'adminStopUser'://点击文章封号
            // this.AdminStopUserFrom = 'article'
            this.showPopupAdminStopUserModel = true
          break;
        }
      },
      /* 举报*/
      fn_radio_report(arg){
        // this.showPopupForReportModel = false
        //判断是否登录
        if(!!this.hasLogin == false){
          this.$toast("您尚未登录")
          return;
        }

        if(!!this.ws_radio_report == true){
          this.$toast("正在举报...")
          return;
        }
        this.ws_radio_report = true
        var params = {
          articleId : this.article.article_id,//文章id
          type : arg,//举报类型
        }
        if(this.clickAOrC == 'c'){//点击了评论
          params.commentId = this.reply_comment_id
        }else if(this.clickAOrC == 'a'){//点击了文章

        }
        this.showPopupForReportModel = false
        /* 遮罩层*/
        this.isDoing = true
        /* 请求*/
        this.$api.article.report(params).then(
            res =>{
              /* 遮罩层*/
              this.isDoing = false
              this.ws_radio_report = false
                if(res.data == 'success'){
                  // this.showPopupForReportModel = false
                  this.$toast("举报成功！")
                }
            }
        )
        /* ********************/
      },
      /* 挂载*/
      mountedFn(){
        this.firstEnter ++
        // 缓冲显示
        this.isBuffering = true
         // 获取文章的唯一id
         var params ={
         	articleId : this.$route.query.articleId
         }
         // 阅读文章id
         this.$api.article.read(params).then(
             res =>{

               // 缓冲消失
               this.isBuffering = false
               this.getArtRes = true
                 switch(res.data){
                   case "not_found":
                       this.$toast("没有该文章")
                       this.article = {}
                       this.$refs.tempDes.innerHTML = '文章消失了'
                       this.finished = true
                       // this.list = []
                       this.$router.replace({
                         path : '/notFound'
                       })
                       break;
                    default:
                    // this.articleUserIdHandler(res.data.article_user_id)
                    // this.articleIdHandler(res.data.article_id)
                    //console.log(this.IAmAuthor,res.data.article_user_id,this.userInfo.user_id,'当前用户id')
                    if(res.data.article_user_id == this.userInfo.user_id){
                       this.IAmAuthor = true
                       // this.IAmAuthorHandler('编辑')
                    }else{
                      this.IAmAuthor = false
                    }
                    this.article = res.data;
                    /* 触发评论加载*/
                    // this.finished = false
                    console.log(res.data,"文章全部信息")
                    // 日期处理
                    var a = res.data.article_publish_time.slice(0,19)
                    var b = a.replace('T', ' ')
                    this.article.article_publish_time = b
                    // 这是关于内容
                    // this.$refs.tempDes.innerHTML = res.data.article_content
                    // this.$refs.tempDes.innerHTML = res.data.article_contentHTML
                    this.$refs.tempDes.innerHTML = res.data.article_contentHTML
                    this.$nextTick(() => {
                      // this.$refs.tempDes.innerHTML = res.data.article_contentHTML
                      var imgs = this.$refs.tempDes.getElementsByTagName("img")
                      for(let j= 0; j<imgs.length;j++){
                        this.imgsSrc.push(imgs[j].src)
                      }
                      console.log(this.imgsSrc,'imgs')
                    })

                    // 字数
                    this.article.article_font_count = this.$refs.tempDes.innerText.length
                    // 将文章标题同步到导航栏
                    // this.titleHandler(res.data.article_plate)
                    /* default结束*/
                 }

             }
         )
         ////////////////////////////////////////////////////收藏
         if(!!this.hasLogin == false){
           this.$toast("您尚未登录")
           // return;
         }else{
           // 获取赞接口
           // this.Waiting_res = true
           this.$api.article.getCol(params).then(
               res =>{
                 this.ws_colHandler = false
                   if(res.data == "isCol"){
                     this.hasCol = true
                   }else{
                     this.hasCol = false
                   }

               }
           )
         }
