                // this.showPopupForChatModel = false
              }else if(res.data.admin_stop){
                res.data.admin_stop = this.$formatDate(new Date(res.data.admin_stop))
                this.$toast(`您被管理封号至${res.data.admin_stop},请注意言行`)
                // this.showPopupForChatModel = false
              }else{
                  this.$toast("发表成功")
                  /* */
                  this.article.article_comment_count +=1
                  res.data.comment_time = this.$formatDate(new Date(res.data.comment_time))
                  if(this.commentSort == 'last'){
                    this.list.unshift(res.data)
