      fn_delComment(){
        // this.showPopupForCommentModel = false
        this.$dialog.confirm({
          title: '您确定删除此条评论吗?',
          message: '删除此条评论'
        }).then(() => {
          if(this.ws_delComment == true){
            this.$toast("正在删除中...")
            return;
          }
          this.ws_delComment = true
          var params = {
            comment_id : this.reply_comment_id,
            article_id : this.article.article_id
          }
          /* 遮罩层*/
          this.isDoing = true
          this.$api.article.delComment(params).then(
              res =>{
                /* 遮罩层*/
                this.isDoing = false
                this.ws_delComment = false
                  switch(res.data){
                    case 'no_power':
                    this.$toast("您的权限不足")
                    break;
                    case 'del_success':
                        /* */
                        for(let i=0;i<this.list.length;i++){
                          if(this.list[i].comment_id == this.reply_comment_id){
                            this.list[i].comment_content = ''
                            // this.list[i].deleted = true
                          }
                        }
                        /* */
                        this.$toast("删除成功")
                        this.showMoreOption = ''

                        this.showPopupForCommentModel = false
                        break;
                    default:

                     //default结束
                  }
              }
          )
        }).catch(() => {
          // on cancel
        });
