      getArticle(params,num){
        /* ***********************************************************************************/
        // this.$api.article[`${this.comReq[num]}`](params).then(
		this.$api.article.getComment(params).then(
            res =>{
              console.log(res.data,'评论')
              // 加载状态结束
              this.loading = false;
              //文章触发加载
              this.finished = false
              if(res.data =="no_data" ){
                //文章不再触发加载
                this.finished = true
              }else if(res.data.finished){
                // alert(1)
                //如果后端告知无需刷新
                this.finished = true
                this.getFn(res.data.result,num)
              }else if(res.data.ok){
                this.getFn(res.data.result,num)
              }else{
                this.finished = true
              }
            }
        )
        /* ***********************************************************************************/
      },
      getFn(arg,num){
           for(var i =0;i<arg.length;i++){
             // var a = arg[i].comment_time.slice(0,23)
             // var b = a.replace('T', ' ')
             // var c = new Date(b)
             // arg[i].comment_time = this.formatDate(new Date(c.setHours(c.getHours() + 8)))
             arg[i].comment_time = this.$formatDate(new Date(arg[i].comment_time))
             // if(this.commentSort == 'last'){
             //   arg[i].commentIndex = this.article.article_comment_count-i-(this.list.length)
             // }else if(this.commentSort == 'first'){
             //   arg[i].commentIndex = i+1+(this.list.length)
             // }
           }
          // default
          /* 数字*/
           switch(num){
            case 0:
                this.list = arg
                break;
            case 1:
                this.list = this.list.concat(arg)
                break;
              default:
           }
      },
      onLoad() {
        // alert(1)
        if(!!this.list[0] == true){
          // alert(this.list[this.list.length-1].comment_time)
          var params = {
            lookAuthor : this.lookAuthor,
            // articleUserId : this.article.article_user_id,
            articleId : this.$route.query.articleId,
            time : this.list[this.list.length-1].comment_time,
            commentSort : this.commentSort
          }
          this.getArticle(params,1)
        }else{
          var params ={
            lookAuthor : this.lookAuthor,
            // articleUserId : this.article.article_user_id,
          	articleId : this.$route.query.articleId,
            commentSort : this.commentSort
          }
          this.getArticle(params,0)
        }
